/* ---- App Body ---- */

.App {
  text-align: center;
  padding-bottom: 100px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-image: url('http://visionsmithstudio.com/wp-content/uploads/2017/08/blue.jpg');
  background-repeat:no-repeat;
  background-size:cover;
  background-position:center;
  min-height: 150px;
  padding: 10px;
  color: white;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.greeting-text-upper {
  margin-bottom: 20px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* ---- Nav Bar ---- */

.navbar-toggle {
  float: none;
}

.nav-vertical-align {
  float: none;
}

.nav-list {
font-size: 1.1em;
}

.nav-item {
  margin-right: 20px;
}

.nav {
  background-color: #2f4f4f;
  opacity: .9;
  border-top-right-radius: 0px;
  border-top-left-radius: 0px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

.nav-link {
  color: #fff !important;
}

.nav-link:hover {
  color: #939393 !important;
}

#newItem {
  color: #d73232;
}


/* ---- Greeting ---- */

#greeting-body {
  min-height: 70px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.card-below {
  margin-top: 20px;
}

.card {
  background-color: #c5ccd2;
  border: 1px solid #829595;
  -webkit-box-shadow: 5px 5px 5px #829595;
          box-shadow: 5px 5px 5px #829595;
}

.card-deck {
  width: 100%;
}

.card-img-top{
  width:100%;
  height:auto;
}

.card-text {
  color: #2f4f4f;
}

.text-muted {
  color: #2f4f4f;
}

.greeting-text {
  color: #2f4f4f;
}

.greeting-subtitle {
  padding-top: 20px;
  margin-bottom: 30px;
}

.jumbotron {
  background: transparent;
  background-image: url('./images/grey90.png');
}

#invisible{
  opacity: .0;
}

/* ---- Youtube Search App ---- */

#ytMain {
  padding-top: 20px;
}

.yt-greeting {
  color: #2f4f4f;
  background-image: url('./images/grey90.png');
  border-radius: 3px;
  padding-top: 20px;
}

.search-bar {
  margin: 20px;
  text-align: center;
}

.search-bar input {
  height: 35px;
  border-radius: 4px;
  margin: auto;
}
.video-item img {
  max-width: 64px;
}

.video-detail {
  color: #2f4f4f;
  margin-top: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.list-group {
  margin-bottom: 50px;
}

.list-group-item:hover {
  background-color: #eee;
}


/* ---- Weather App ---- */

.weather-app-body {
  padding-bottom: 300px;
}

.weather-main {
  padding-top: 15px;
  background: transparent;
  background-image: url('./images/grey90.png');
  padding-bottom: 300px;
  border-radius: 7px;
  color: #2f4f4f;
}

td, th {
  vertical-align: middle;
  text-align: center;
}

.input-group {
  margin: 20px 0px;
}

svg {
  height: 150px;
}

td:first-of-type, td:first-of-type > div {
  height: 200px;
  width: 250px;
}

h1 {
  text-align: center;
}

.weather-search {
  height: 38px;
  text-align: center;
  border-radius: 3px !important;
  width: 70% !important;
}

#weatherInput {
  margin-left: 3px;
  border-radius: 3px;
}

.weather-search-form {
max-width: 800px;
display: -ms-flexbox;
display: flex;
margin-left: auto;
margin-right: auto;
-ms-flex-pack: center;
    justify-content: center;
}

.weather-search-container {
display: block;
text-align: center;
padding-right: 80px;
padding-bottom: 30px;
}

.weather-title {
  padding-top: 20px;
}

/* ---- To Do List ---- */


.toDoAppBody {
  margin-top: 20px;
  width: 600px;
  color: #2f4f4f;
}

.toDo {
  padding-top: 20px;
}

.toDo-input {
  max-width: 50%;
}

label {
  font-size: 20px;
}

#ToDoSubmitBtn {
  background-color: green;
  border: none;
  outline:none;
  color: white;
  font-size: .9em;
  border-radius: 5px;
}

.ToDoBtn {
font-size: .5em;
padding: 2px 4px 2px 4px;
float: right;
}

.toDo-label {
  margin-right: 8px;
}

.category-items {
  list-style: none;
}

li {
  display: inline;
}

#catLabel {
  padding-top: 10px;
}

#catOpt {
     width: 30%;
     text-align: center;
     padding-left: 4%;
}

/* ---- Translator App ---- */



#transform {
  border-radius: 10px;
  border: 1px #ccc solid;
  -ms-flex-pack: center !important;
      justify-content: center !important;
}

.TranslateForm {
  margin-top: 20px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.translator {
  margin-left: auto;
  margin-right: auto;
}

#selectedLanguage {
  background-color: #fafafa;
  padding-left: 20px;
  margin-right: 10px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

#inputPhrase {
  padding-right: 15px;
  padding-left: 15px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}

#translatedWord {
  font-size: 4em;
}

#translateBody {
  margin-left: 2%;
  margin-right: 2%;
  color: #2f4f4f;
  border-radius: 5px;
  background-image: url('./images/grey90.png');
  margin-top: 20px;
  margin-bottom: 400px;
  padding-top: 20px;
  padding-bottom: 50px;
  padding-left: 10px;
  padding-right: 10px;
}

