@-webkit-keyframes zoom {
0% { -webkit-transform:scale(1,1); transform:scale(1,1); }
50% { -webkit-transform:scale(1.03,1.03); transform:scale(1.03,1.03); }
100% {
    -webkit-transform:scale(1,1);
            transform:scale(1,1);
  }
}

@keyframes zoom {
0% { -webkit-transform:scale(1,1); transform:scale(1,1); }
50% { -webkit-transform:scale(1.03,1.03); transform:scale(1.03,1.03); }
100% {
    -webkit-transform:scale(1,1);
            transform:scale(1,1);
  }
}

